.story {
    background-color: gray;
    justify-items: center;
    align-items: center;
    min-width: 200px;
    height: 20vh;
    overflow: hidden;
    margin-bottom: 0.5rem;
    position: relative;
}

.story figure {
    width: 105%;
    height: 105%;
    /* change min-height OR change the resolution of the images aka no wide images */
    margin: 0;
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 1s ease;
    background-position: bottom;
}

.story figure:hover {
    width: 111%;
    height: 111%;
    cursor: pointer;
}

.story-color-overlay {
    height: 100%;
    width: 100%;
}

