@-webkit-keyframes bounce {

  from,
  20%,
  53%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  40%,
  43% {
    -webkit-animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    -webkit-transform: translate3d(0, -15px, 0);
    transform: translate3d(0, -15px, 0);
  }

  70% {
    -webkit-animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    -webkit-transform: translate3d(0, -15px, 0);
    transform: translate3d(0, -15px, 0);
  }

  90% {
    -webkit-transform: translate3d(0, -4px, 0);
    transform: translate3d(0, -4px, 0);
  }
}

@keyframes bounce {

  from,
  20%,
  53%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  40%,
  43% {
    -webkit-animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    -webkit-transform: translate3d(0, -15px, 0);
    transform: translate3d(0, -15px, 0);
  }

  70% {
    -webkit-animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    -webkit-transform: translate3d(0, -15px, 0);
    transform: translate3d(0, -15px, 0);
  }

  90% {
    -webkit-transform: translate3d(0, -4px, 0);
    transform: translate3d(0, -4px, 0);
  }
}

.up-arrow:hover .arrow-glyph {
  -webkit-animation-name: bounce;
  animation-name: bounce;
  -webkit-transform-origin: center bottom;
  transform-origin: center bottom;
  color: #474c50;
}

.arrow-glyph {
  display: block;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@media screen and (min-width:950px) {
  .up-arrow {
    top: -25%;
    right: 10%;
  }
}
nav {
  background-color: #fff;
  display: flex;
  width: 100%;
  min-height: 44px;
  position: relative;
  /* z-index: 1; this was the default but i changed it to 3 so that the drop down is not overlayed by the up ->*/
  z-index: 3;
}

.nav-list {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  max-height: 800px;
  transition: all 500ms ease-in-out;
  overflow: hidden;
  padding: 0;
}

.nav-list-collapsed {
  max-height: 0;
}

.nav-list-hide-li .nav-list-item,
.arrowhead {
  /* sets display to none thus removes the items from tab order */
  display: none;
}

.nav-list-item {
  list-style-type: none;
  margin: 0.2rem 0;
  display: flex;
  flex-flow: column nowrap;
}

.nav-list li:first-of-type {
  margin-top: 1.5rem;
}

.nav-link {
  text-decoration: none;
  color: black;
  padding: 0.5rem;
  font-size: 1.1rem;
  display: inline-block;
  max-height: 1rem;
}

.active {
  color: black;
  font-weight: bold;
}

.hamburger-menu {
  height: 44px;
  width: 44px;
  position: absolute;
  right: 0;
  background-image: url(/static/media/Hamburger_icon.86595150.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: white;
  border: none;
  transition: background-color 500ms;
}

.hamburger-menu:active {
  background-color: gray;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.nav-link:hover {
  text-decoration: underline;
  color: grey;
}

.nav-link:focus,
.hamburger-menu:focus {
  outline: none;
  box-shadow: 0 0 5px lightsteelblue;
  border: 1px solid darkgrey;
}

.my-story {
  color: #821919;
}

/* MY DROP-DOWN MENU */
/* button */

.drop-down-menu {
  display: flex;
  flex-flow: column nowrap;
  padding: 0;
  margin: auto;
  max-height: 0;
  transition: max-height 400ms ease;
}

.drop-down-hide {
  max-height: 0;
}

.drop-down-hide li {
  display: none;
}

.drop-down-expand {
  /* max-height: 300px; */
  /* min-height = 2rem for each item in the dropdown */
  min-height: calc(2rem * 10);
  width: 100%;
}

.drop-down-item {
  margin: 0 !important;
  padding: 0 !important;
  max-width: 200px;
  display: inline-block;
  width: 200px;
  font-size: 0.9rem;
  display: flex;
  max-height: 2rem;
  width: 100%;
  max-width: 100%;
}

.drop-down-item a {
  text-decoration: none;
  width: 100%;
  background: #f2f2f2;
  padding: 0.5rem 0;
  border: 1px dotted #8080804f;
  border-width: 1px 0 1px;
  max-height: 1rem;
  color: black;
}

.drop-down-item:focus {
  text-decoration: none;
  outline: none;
  box-shadow: none;
  border: none;
}

.drop-down-item a:focus {
  text-decoration: none;
  outline: none;
  box-shadow: 0 0 5px lightsteelblue;
  border: 1px solid darkgrey;
}

.drop-down-item a:hover {
  background-color: #fff;
  text-decoration: underline;
  color: grey;
}

.li-dropdown-container {
  position: relative;
  max-height: 100%;
}

/* Projects Submenu */
.nav-link-dropdown {
  position: relative;
  font-family: inherit;
  white-space: nowrap;
  text-decoration: none;
  padding: 0.25rem 0.5rem;
  margin: 0.25rem;
  cursor: pointer;
  background-color: #fff;
  color: #82898f;
  border: none;
  text-decoration: none;
  color: black;
  padding: 0.5rem;
  font-size: 1.1rem;
  display: inline-block;
  margin: 0;
}

.nav-link-dropdown:focus {
  outline: none;
  box-shadow: 0 0 5px lightsteelblue;
  border: 1px solid darkgrey;
}

.nav-link-dropdown:hover {
  text-decoration: underline;
  color: grey;
}

@media screen and (min-width: 600px) {
  .drop-down-menu {
    color: black;
    outline: none;
    padding: 0 0rem 0 0 !important;
    position: absolute;
    background: #ebebeb;
    display: flex;
    flex-flow: column nowrap;
    width: 200px;
    text-align: left;
    transition: height 350ms ease;
    height: 0;
    overflow: hidden;
  }

  .arrowhead {
    display: inline;
    display: initial;
    color: #424141;
    font-size: 12px;
  }

  .drop-down-hide {
    height: 0;
  }

  .drop-down-hide li {
    display: none;
  }

  .drop-down-expand {
    border: 1px solid #d0d0d0;
  }

  .drop-down-item a {
    border: none;
  }

  nav {
    height: 4.5rem;
  }

  .nav-list-collapsed {
    height: auto;
  }

  .nav-list-hide-li .nav-list-item {
    display: inline;
    display: initial;
  }

  .nav-list-item {
    display: inline;
    display: initial;
  }

  .hamburger-menu {
    display: none;
  }

  .nav-list {
    flex-flow: row nowrap;
    height: auto;
    padding: 0;
    overflow: visible;
    overflow: initial;
  }

  .nav-link {
    padding: 0.5rem 0.4rem;
  }

  .nav-list li {
    margin: 0.25rem 0 0.5rem;
  }

  .nav-list li:last-of-type {
    margin-left: auto;
    margin-right: 0;
  }

  .nav-list li:first-of-type {
    margin: 0.25rem 0 0.5rem;
  }
}

@media screen and (min-width: 950px) {
  .nav-link {
    padding: 0.5rem 0.8rem;
  }
}

