/* START list with badges */
.bullet-list-new {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin-left: 0;
  padding: 0;
}

.bullet-list-item-new {
  list-style-type: none;
}

.bullet-list-item-new:hover {
  animation-name: rubberBand;
  list-style-type: none;
}

.card-text {
  margin: 1rem 5px 1rem;
}

.badge-link:hover {
  text-decoration: underline;
}

.bullet-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  /* justify-content: space-evenly; looks better but Edge doesn't support it */
}

.bullet-list-item {
  margin-left: 1.5rem;
}

.icon-svg-container {
  width: 80px;
  height: 100px;
  margin: auto;
}

.budges {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
  /* justify-content: space-evenly; looks better but Edge doesn't support it */
}


.about-main {
  width: 100%;
  margin: 3rem auto auto;
  max-width: 1600px;
}

.about-main-header {
  font-size: 2.5rem;
  margin-bottom: 0;
  vertical-align: bottom;
}

.about-secondary-header {
  margin: -0.25rem 0 0 0;
  color: #1d5cbd;
  font-size: 2rem;
}

.about-header {
  font-size: 1.5rem;
  margin-bottom: 0;
}

.about-container {
  margin-bottom: 4rem;
  display: flex;
  flex-flow: column;
}

.about-container-details {
  width: calc(100% - 1rem);
  margin: 0 auto 2rem;
  padding-bottom: 3rem;
  border-bottom: 1px lightgray dashed;
}

.about-aside {
  width: calc(100% - 10px);
  min-width: 40%;
  display: flex;
  flex-flow: column nowrap;
  margin: 1rem auto 1rem;
  padding-top: 0.5rem;
}

.card {
  width: calc(100% - 2px);
  min-width: 30%;
  display: flex;
  flex-flow: column nowrap;
  margin: 0 auto 1rem;
}



.card-image {
  max-width: 75%;
  border-radius: 5px 5px 0 0;
  margin: 5px auto 0 auto;
}

.card-caption {
  margin: 0 auto 5px auto;
  border: 1px solid lightslategrey;
  width: calc(75% - 2px);
  border-radius: 0 0 5px 5px;
  background-color: lightgray;

}



@media screen and (min-width: 600px) {
  .about-main {
    margin-bottom: auto;
  }

  .card-image {
    width: 55%;
  }

  .card-caption {
    width: calc(55% - 2px);
  }

  .about-header {
    font-size: 2rem;
  }
}

@media screen and (min-width: 1000px) {
  .about-container {
    flex-flow: row;
  }

  .about-container-details {
    padding-bottom: 0;
    border-bottom: none;
  }

  .about-aside {
    width: 40%;
    border: none;
    margin: 0 auto 1rem;
  }

  .card-image {
    width: 65%;
  }

  .card-caption {
    width: calc(65% - 2px);
  }

}